<template>
  <div id="gdmap"></div>
</template>
<script>
async function loadGD() {
  return new Promise(resolve => {
    if (window.GDInited) {
      return resolve();
    }

    let url =
      "https://webapi.amap.com/maps?v=1.4.15&key=3fc671c85033d432267ff29ae143c1a1&callback=onGDMap";

    let jsapi = document.createElement("script");

    jsapi.charset = "utf-8";
    jsapi.src = url;

    window.onGDMap = function() {
      window.GDInited = true;
      window.onGDMap = null;
      resolve();
    };

    document.head.appendChild(jsapi);
  });
}

export default {
  data() {
    return {
      map: undefined
    };
  },
  beforeCreate() {
    loadGD().then(() => this.init());
  },
  methods: {
    init() {
      this.map = new window.AMap.Map("gdmap", {
        center: [111.000627, 35.025643],
        mapStyle: "amap://styles/4758ab17988985729efccaa2e50677a0",
        scrollWheel: true
      });

      this.$unlock();
    }
  }
};
</script>
<style scoped>
#gdmap {
  width: 100%;
  height: 100%;
}
</style>
<style>
.amap-logo,
.amap-copyright {
  display: none !important;
  bottom: -1000px;
}
</style>